@mixin adaptiv-value($indentType, $pcSize, $mobSize, $maxSize) {
	$addSize: $pcSize - $mobSize;
	@if $maxSize==1 {
		//Только если меньше контейнера
		#{$indentType}: $pcSize + px;
		@media (max-width: #{1200 + px}) {
			#{$indentType}: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{1200 - 320}));
		}
	} @else if $maxSize==2 {
		//Только если больше контейнера
		#{$indentType}: $pcSize + px;
		@media (min-width: #{1200 + px}) {
			#{$indentType}: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{1440 - 320}));
		}
	} @else {
		//Всегда
		#{$indentType}: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{1440 - 320}));
	}
}

// BEAMS SECTION
.beams-section {
    background: url('../img/noise.png') fixed;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background: #010101;
        opacity: 0.99;
    }

    .beams {
        z-index: 2;
    }
    .tables {
        width: 100%;
        margin: calc(100vh - 356px) 0 0 0;
        transform: translateX(120%);
        z-index: 2;
    }
    .table-1 {
        transform-origin: 50% 100%;
    }
    .parallax-swimmer {
        z-index: 2;
    }
    .drops {
        z-index: 1;
    }
    .tables-wrapper {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        position: relative;
        .table-con {
            position: relative;
            flex: 0 0 calc(100% / 3);
            display: flex;
            justify-content: center;
        }
        .second-con {
            margin: 0 20px;
            max-width: 100%;
            flex: 0 0 calc((100% / 3) - 40px);
        }
        .third-con {
            right: calc((-100% / 3) / 2);
        }
        .first-con {
            right: calc((100% / 3) / 2);
        }
    }
}
// ===

// FORM SECTION
.form-section {
    background: #088ECB;

    .footer {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .side-1 {
            font-size: 20px;
            font-weight: 400;
            color: #fff;
        }
    }
    &__content {
        display: flex;
        justify-content: space-between;
        margin-bottom: 156px;

        .contacts-wrapper {
            flex: 0 0 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            p {
                font-size: 64px;
                font-weight: 400;
                color: #fff;
                margin-bottom: 44px;
            }
            .contacts-links {
                list-style-type: none;

                li:not(:last-child) {
                    margin-bottom: 24px;
                }
                a {
                    font-size: 24px;
                    font-weight: 400;
                    color: #fff;
                    text-decoration: none;
                }
            }
            .contacts-social {
                list-style-type: none;
                display: flex;
                
                li:not(:last-child) {
                    margin-right: 24px;
                }
                a {
                    display: inline-flex;
                    width: 64px;
                    height: 64px;
                    border-radius: 100%;
                    border: 1px solid #fff;
                    position: relative;

                    img {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
            }
        }
        .form-wrapper {
            flex: 0 0 50%;

            .form {
                display: flex;
                flex-direction: column;

                &-input {
                    width: 100%;
                    font-size: 64px;
                    font-weight: 600;
                    color: #fff;
                    margin: 0 0 40px 0;
                    border: none;
                    border-bottom: 1px solid #fff;
                    background: none;
                    outline: none;
                    padding-bottom: 4px;

                    &::placeholder {
                        -webkit-text-stroke: 1px #fff;
                        color: transparent
                    }
                }
                &-button {
                    width: fit-content;
                    border: none;
                    background: none;
                    text-align: left;
                    font-size: 64px;
                    font-weight: 600;
                    color: #fff;
                    position: relative;
                    cursor: pointer;

                    &:after {
                        content: '';
                        width: 72px;
                        height: 64px;
                        background: url('../img/icons/send-arrow.svg') center center no-repeat;
                        position: absolute;
                        top: 50%;
                        left: calc(100% + 24px);
                        transform: translate(0, -50%);
                        
                    }
                }
            }
        }
    }
}
