// INFO SECTION
.info-section {
    background: #010101;
}
.article-block .content {
    display: flex;
    gap: 186px;

    .image-side {
        flex: 0 0 584/1200 * 100%;
    }
    .content-side {
        flex: 0 0 430/1200 * 100%;
        display: flex;
        flex-direction: column;
    }
}
.text {

    &_border {
        @include adaptiv-value('padding-top', 80, 30, 1);
        @include adaptiv-value('padding-bottom', 80, 30, 1);
        border-top: 1px solid #828282;
        border-bottom: 1px solid #828282;
    }
    p {
        font-family: 'Inter', sans-serif;
        color: #fff;
        font-size: 20px;
        font-weight: 400;
        line-height: calc(32 / 20 * 100%);
    }
    p:not(:last-child) {
        margin-bottom: 24px;
        
    }
}
.text-overflow {
    -webkit-text-stroke: 1px #fff;
    color: transparent;
    font-size: 65px;
    font-weight: 700;
    line-height: calc(78 / 65 * 100%);
    @include adaptiv-value('margin-bottom', 80, 40, 1);
    position: relative;

    &:before {
        content: attr(data-fill-text);
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        color: #fff;
        height: 0;
    }
}
.image-mask {
    overflow: hidden;
}
.parallax-container {
    display: flex;
    justify-content: flex-end;
}
.parallax-item {
    flex: 0 0 738/1200 *100%;

    img {
        margin-top: 80px;
    }
    h2 {
        font-weight: 400;
        font-size: 80px;
        line-height: calc(96 / 80 * 100%);
        color: #088ECB;

        &::first-line {
            color: #fff;
        }
    }
}
// ===

// ARTICLE SECTION
.article-section {
    .article-block {
        margin-top: 120px;
    }
}
