// SLIDER
.slide {
    display: flex;

    &__description {
        border-bottom: 1px solid #828282;
        padding-bottom: 45px;
        margin-bottom: 80px;

        p {
            color: #828282;
            text-transform: uppercase;
            font-size: 10px;
            font-weight: 400;
        }
    }
    &__content {
        flex: 0 0 430 / 1200 * 100%;

        .quote-block {
            border-bottom: 1px solid #828282;
            padding-bottom: 64px;
            img {
                margin-bottom: 64px;
            }
            p {
                font-size: 20px;
                color: #828282;
                line-height: calc(32 / 20 * 100%);
                font-style: italic;
            }
        }
    }
    &__image {
        flex: 1 1 100%;
        padding-left: 32px;
        display: flex;
        justify-content: flex-end;
        
        .image-wrapper {
            clip-path: circle(50% at 50% 50%);
            display: inline-flex;
            position: relative;
            
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: #548BCB;
                mix-blend-mode: color;
                z-index: 2;
            }
            img {
                clip-path: circle(50% at 50% 50%);
                position: relative;
            } 
        }
    }
}
